* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Nunito', sans-serif;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#533362), to(#202039));
  background-image: -webkit-linear-gradient(top, #533362, #202039);
  background-image: -o-linear-gradient(top, #533362, #202039);
  background-image: linear-gradient(to bottom, #533362, #202039);
  height: 100%;
}

#root {
  height: 100%;
}

button {
  font-family: 'Nunito', sans-serif;
}

a {
  color: #ffffff;
}

.button {
  background-color: rgba(255, 255, 255, 0.16);
  border: 0;
  color: #ffffff;
  font-size: 32px;
  padding: 15px 45px;
  border-radius: 6px;
  cursor: pointer;
}

.main {
  height: 100%;
}

.bottom-space {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  pointer-events: none;
}

.space-man {
  position: absolute;
  right: 10%;
  top: 10%;
  -webkit-animation: floating 60s infinite;
          animation: floating 60s infinite;
  z-index: -1;
}

.spaceship-container {
  position: absolute;
  left: 5%;
  top: 5%;
  -webkit-animation: flying 15s linear infinite;
          animation: flying 15s linear infinite;
}

.spaceship__fire {
  position: absolute;
  bottom: 11px;
  left: 9px;
  -webkit-transform: rotate(4deg);
      -ms-transform: rotate(4deg);
          transform: rotate(4deg);
  -webkit-animation: fire 0.2s linear infinite;
          animation: fire 0.2s linear infinite;
}

@-webkit-keyframes floating {
  from {
    -webkit-transform: rotate(20deg) translateY(0px) scale(1);
            transform: rotate(20deg) translateY(0px) scale(1);
  }
  50% {
    -webkit-transform: rotate(-10deg) translateY(60px) scale(0.4);
            transform: rotate(-10deg) translateY(60px) scale(0.4);
  }
  to {
    -webkit-transform: rotate(20deg) translateY(0px) scale(1);
            transform: rotate(20deg) translateY(0px) scale(1);
  }
}

@keyframes floating {
  from {
    -webkit-transform: rotate(20deg) translateY(0px) scale(1);
            transform: rotate(20deg) translateY(0px) scale(1);
  }
  50% {
    -webkit-transform: rotate(-10deg) translateY(60px) scale(0.4);
            transform: rotate(-10deg) translateY(60px) scale(0.4);
  }
  to {
    -webkit-transform: rotate(20deg) translateY(0px) scale(1);
            transform: rotate(20deg) translateY(0px) scale(1);
  }
}

@-webkit-keyframes flying {
  0% {
    -webkit-transform: translate(-200px, 200px) rotate(-10deg);
            transform: translate(-200px, 200px) rotate(-10deg);
  }
  20% {
    -webkit-transform:  translate(100px, -200px) rotate(-10deg);
            transform:  translate(100px, -200px) rotate(-10deg);
  }
  100% {
    -webkit-transform:  translate(100px, -200px) rotate(-10deg);
            transform:  translate(100px, -200px) rotate(-10deg);
  }
}

@keyframes flying {
  0% {
    -webkit-transform: translate(-200px, 200px) rotate(-10deg);
            transform: translate(-200px, 200px) rotate(-10deg);
  }
  20% {
    -webkit-transform:  translate(100px, -200px) rotate(-10deg);
            transform:  translate(100px, -200px) rotate(-10deg);
  }
  100% {
    -webkit-transform:  translate(100px, -200px) rotate(-10deg);
            transform:  translate(100px, -200px) rotate(-10deg);
  }
}

@-webkit-keyframes fire {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes fire {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}


